import React, { useState } from "react";
import API from "../../utils/API"


function OrderReceived(){
    const[fields,setFields]=useState({
        email:'',
        result:[]
    })


    const renderOrders=(e)=>{

        e.preventDefault();
        API.getSellerOrder(fields.email).then(res=>{
            console.log(res)
        setFields({fields:{result:res.data}})})

    }

    const handleInputChange= e=>{

        e.preventDefault();
        
        setFields({ fields:{email:e.target.value} });
    
      }

    const displayOrders=()=>
    {
       
       return( fields.result.map(elem=>
        <div className="card oreder-card">
             <div className="row">
                <div className="col-sm-4">
                    <img src={`/api/new/file/${elem.fileId}`} className="imgTumbnail" alt=""></img>
                </div>
                <div className="col-sm-8">
                    <a className="mt-4 ml-4 itemLink" href={`/displayItem/${elem.fileId}`} alt=""><h4>{elem.orderTitle}</h4></a><hr/> 
                    <p> Description : {elem.orderInfo}</p>
                </div>
            </div>       
        </div>

        ))
    }


    return(

        <div className="container">
            <div className="row justify-content-center">
                <form onSubmit={renderOrders}>
                    <div className="mt-3">
                        <label className="ml-2">Please enter your Seller Email</label>
                        <input className="form-control" name="email" value={fields.email} onChange={handleInputChange}/>
                    </div>
                    <button className="btn m-3">Find</button>
                </form>
            </div>
            <div className="row">
                {fields.result ? ( displayOrders)
                    :<div>No Order received yet</div>}
               
            </div>
        </div>
    )
}

export default OrderReceived